import Vue from 'vue';
import Vuex from 'vuex';
import youtubeService from '../services/youtube.service'

Vue.use(Vuex);
export default {
    strict: true,
    state: {
        topFivers: {},
        search: {
            str:'',
            results:[]
        }
    },
    mutations: {
        setTopFives(state, { topFives }) {

            state.topFivers = topFives
        },
        setSearchResults(state, { searchResults }) {
            state.search.str=searchResults.str
            state.search.results=searchResults.results
        }
    },
    actions: {
        getTopFives({ commit }) {
            return youtubeService.getTopFives()
                .then(topFives => {
                    commit({ type: 'setTopFives', topFives })
                    return topFives
                })
        },
        search({ commit }, { searchStr }) {
            youtubeService.search(searchStr)
                .then(searchResults => {
                    commit({ type: 'setSearchResults', searchResults })
                    return Promise.resolve(true)
                })
        }
    },
    getters: {
        getSearchResults(state){
            if(!state.search) return 'no search'
            return state.search
        }
    }
};
