import storageService from './storage.service.js'
import youtubeService from './youtube.service.js'

function getUserData() {
    var userData = storageService.load('userData')
    if (!userData) {
        userData = _createDefaultData()
        storageService.save('userData', userData)
    }
    return Promise.resolve(userData)
}
function removeArtist(artistToRemove){
    var userData = storageService.load('userData')
    let idx=userData.userArtists.findIndex(currArtist=>currArtist.youTubeId===artistToRemove.youTubeId)
    userData.userArtists.splice(idx,1)
    storageService.save('userData', userData)
    return Promise.resolve(artistToRemove)
}
function removePlaylist(playlistToRemove){
    var userData = storageService.load('userData')
    let idx=userData.playlists.findIndex(currPlaylist=>currPlaylist.id===playlistToRemove.id)
    userData.playlists.splice(idx,1)
    storageService.save('userData', userData)
    return Promise.resolve(playlistToRemove)
}
function saveVideoForLater(videoId) {
    var userData = storageService.load('userData')
    userData.saved.videoIds.push(videoId)
    storageService.save('userData', userData)
    return Promise.resolve(userData)
}
function createNewPlaylist(playlistName){
    var userData = storageService.load('userData')
    let newPlaylist={
        id: _makeid(),
        name:playlistName,
        videoIds:[]
    }
    userData.playlists.push(newPlaylist)
    storageService.save('userData', userData)
    return  Promise.resolve(newPlaylist)
}
function addArtist(artistToAdd) {
   return youtubeService.getVideoByChannel(artistToAdd.id)
        .then(artistVideos => {
            let newArtist = {
                name: artistToAdd.name,
                id: _makeid(),
                youTubeId: artistToAdd.id,
                videoIds: artistVideos
            }
            var userData = storageService.load('userData')
            userData.userArtists.push(newArtist)
            storageService.save('userData', userData)
            return newArtist
        })
}
function updatePlaylists(changesToMake) {
    var userData = storageService.load('userData')
    userData.playlists.forEach(currPlaylist => {
        if (changesToMake.listsToRemoveFrom.includes(currPlaylist.id)) {
            let idx = currPlaylist.videoIds.findIndex(currVideoId => currVideoId === changesToMake.videoId)
            currPlaylist.videoIds.splice(idx, 1)
        }
        else if (changesToMake.listsToAddTo.includes(currPlaylist.id)) {
            currPlaylist.videoIds.push(changesToMake.videoId)
        }
    })
    storageService.save('userData', userData)
    return Promise.resolve(changesToMake)
}
function removeVideo(videoToRemove) {
    var userData = storageService.load('userData')
    if (videoToRemove.catagory === 'saved') {
        let idx = userData[videoToRemove.catagory].videoIds.findIndex(item => item === videoToRemove.videoId)
        userData[videoToRemove.catagory].videoIds.splice(idx, 1)
    }
    else {
        let playlistIdx = userData[videoToRemove.catagory].findIndex(currPlaylist => currPlaylist.id === videoToRemove.playlistId)
        let videoIdx = userData[videoToRemove.catagory][playlistIdx].videoIds.findIndex(currVideoId => currVideoId === videoToRemove.videoId)
        userData[videoToRemove.catagory][playlistIdx].videoIds.splice(videoIdx, 1)
    }
    storageService.save('userData', userData)
    return Promise.resolve(userData)

}

export default {
    getUserData,
    removeVideo,
    saveVideoForLater,
    updatePlaylists,
    addArtist,
    removeArtist,
    removePlaylist,
    createNewPlaylist
}
