<template>
  <div class="card-container">
    <slot name="header"></slot>
    <iframe :style="styles" :src="'https://www.youtube.com/embed/'+id+'?controls=1'"></iframe>
    <div class="option-conainer flex align-center space-between">
      <slot name="btn"></slot>
      <slot name="removBtn" class="remove"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: String
  },
  name: "video-card",
  data() {
    return {};
  },
  methods: {
    onBtnClicked(ev) {
      this.$emit("btnClicked", this.id, ev.target.dataset.name);
    }
  },
  computed: {
    styles() {
      if (window.innerWidth < 600)
        return {
          width: "250px",
          height: "150px",
          display: "block"
        };
      return {
        width: "350px",
        height: "250px",
        display: "block"
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.card-container {
  border: 1px solid $cardBorder;
  margin: 10px;
  background-color: $cardBC;
  max-width: 252px;
  .header {
    color: $mainBC;

    h2 {
      margin: 4px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
    .showMore {
      white-space: initial;
      overflow: inherit;
    }
    p {
      margin: 3px;
    }
  }

  iframe {
    width: 250px;
    height: 150px;
    display: block;
  }
  .option-conainer {
    color: $mainBC;
    div {
      padding: 5px;
      border: 1px solid $actionBorder;
      border-radius: 10px;
      margin: 9px 8px;
      cursor: pointer;
    }
    .remove {
      color: red;
    }
  }
}
@media (min-width: 600px) {
  .card-container {
    max-width: 352px;
    iframe {
      width: 550px;
      height: 350px;
    }
    .option-conainer{
    min-height: 70px;

    }
  }
}
</style>
