import axios from 'axios'
function getTopFives() {
    var topFives = [
        {
            musicType: 'metal',
            videoIds: []
        },
        {
            musicType: 'jazz',
            videoIds: []
        },
        {
            musicType: 'pop',
            videoIds: []
        }
    ]
    var baseStr = `https://www.googleapis.com/youtube/v3/search?part=snippet&chart=mostPopular&order=viewCount&videoDuration=medium&regionCode=us&key=AIzaSyCeCwp27m0BhJewknOiDhcuUDY9_cMx5xs&type=video&videoCategoryId=10&q=`
    var prms = topFives.map(musicObj => {
        return axios.get(baseStr + musicObj.musicType)
    })
    return Promise.all(prms)
        .then(res => {
            for (let i = 0; i < res.length; i++) {
                topFives[i].videoIds = res[i].data.items.map(item => item.id.videoId)
            }
            return topFives
        })
}
function getVideoByChannel(channelId) {
    return axios.get(`https://www.googleapis.com/youtube/v3/search?key=AIzaSyCeCwp27m0BhJewknOiDhcuUDY9_cMx5xs&channelId=${channelId}&part=snippet,id&order=date&maxResults=15`)
        .then(res => {
            let videoIds=[]
             res.data.items.forEach(item => {
                if (item.id.videoId!==undefined)
                    videoIds.push(item.id.videoId) 
            })
            return videoIds
        })
}
function search(searchStr) {
    return axios.get(`https://www.googleapis.com/youtube/v3/search?part=snippet&chart=mostPopular&regionCode=us&videoCategoryId=10&maxResults=10&key=AIzaSyCeCwp27m0BhJewknOiDhcuUDY9_cMx5xs&type=video&q=${searchStr}`)
        .then(res => {
            var videos = res.data.items.map(item => {
                return {
                    videoId: item.id.videoId,
                    artistId: item.snippet.channelId,
                    artistName: item.snippet.channelTitle,
                    videoTitle: item.snippet.title
                }
            })
            return {
                str: searchStr,
                results: videos
            }
        })
        .catch(err => console.log(err))

}

export default {
    // testSearch,
    getTopFives,
    getVideoByChannel,
    search
}