import Vue from 'vue';
import Vuex from 'vuex';
// import youtubeService from '../services/youtube.service'
import userService from '../services/user.service'
import youtubeService from '../services/youtube.service';

Vue.use(Vuex);
export default {
    strict: true,
    state: {
        // topFivers:{}
        // newUrl: ''
        userData: {}
    },
    mutations: {
        // resetNewUrl(state) {
        //     state.newUrl = '';           
        // },
        // setTopFives(state, {topFives}){

        //     state.topFivers=topFives
        // }
        setUserData(state, { userData }) {
            state.userData = userData
        },
        removeVideo(state, { videoToRemove }) {
            if (videoToRemove.catagory === 'saved') {
                let idx = state.userData[videoToRemove.catagory].videoIds.findIndex(item => item === videoToRemove.videoId)
                state.userData[videoToRemove.catagory].videoIds.splice(idx, 1)
            }
            else {
                let playlistIdx = state.userData[videoToRemove.catagory].findIndex(currPlaylist => currPlaylist.id === videoToRemove.playlistId)
                let videoIdx = state.userData[videoToRemove.catagory][playlistIdx].videoIds.findIndex(currVideoId => currVideoId === videoToRemove.videoId)
                state.userData[videoToRemove.catagory][playlistIdx].videoIds.splice(videoIdx, 1)
            }
        },
        saveVideo(state, { videoId }) {
            state.userData.saved.videoIds.push(videoId)
        },
        updatePlaylists(state, { changesToMake }) {

            state.userData.playlists.forEach(currPlaylist => {
                if (changesToMake.listsToRemoveFrom.includes(currPlaylist.id)) {
                    let idx = currPlaylist.videoIds.findIndex(currVideoId => currVideoId === changesToMake.videoId)
                    currPlaylist.videoIds.splice(idx, 1)
                }
                else if (changesToMake.listsToAddTo.includes(currPlaylist.id)) {
                    currPlaylist.videoIds.push(changesToMake.videoId)
                }
            })
        },
        updateArtists(state, { newArtist }) {
            state.userData.userArtists.push(newArtist)
        },
        removeArtist(state, { artistToRemove }) {
            let idx = state.userData.userArtists.findIndex(currArtist => currArtist.youTubeId === artistToRemove.youTubeId)
            state.userData.userArtists.splice(idx, 1)
        },
        removePlaylist(state,{playlistToRemove}){
            let idx=state.userData.playlists.findIndex(currPlaylist=>currPlaylist.id===playlistToRemove.id)
            state.userData.playlists.splice(idx,1)
        },
        addNewPlaylist(state, {newPlaylist}){
            state.userData.playlists.push(newPlaylist)
        }
    },
    actions: {
        getUserMusic({ commit }) {
            userService.getUserData()
                .then(userData => {
                    commit({ type: 'setUserData', userData })
                })
        },
        removeVideo({ commit }, { videoToRemove }) {
            return userService.removeVideo(videoToRemove)
                .then(updatedData => {
                    commit({ type: 'removeVideo', videoToRemove })
                    return updatedData
                })
        },
        saveVideoForLater({ commit }, { videoId }) {
            userService.saveVideoForLater(videoId)
                .then(() => commit({ type: 'saveVideo', videoId }))

        },
        handleListsFromModal({ commit }, { changesToMake }) {
            return userService.updatePlaylists(changesToMake)
                .then(changesToMake => {
                    commit({ type: 'updatePlaylists', changesToMake })
                    return Promise.resolve(true)
                })
        },
        addArtist({ commit }, { artistToAdd }) {
            userService.addArtist(artistToAdd)
                .then(newArtist => commit({ type: 'updateArtists', newArtist }))
        },
        removeArtist({ commit }, { artistToRemove }) {
            return userService.removeArtist(artistToRemove)
                .then(artistToRemove => {
                    commit({ type: 'removeArtist', artistToRemove })
                    return Promise.resolve(true)
                })
        },
        removePlaylist({commit},{playlistToRemove}){
           return userService.removePlaylist(playlistToRemove)
            .then(playlistToRemove=>commit({type:'removePlaylist', playlistToRemove}))
            
        },
        createNewPlaylist({commit}, {playlistName}){
            userService.createNewPlaylist(playlistName)
            .then(newPlaylist=>commit({type:'addNewPlaylist', newPlaylist}))
        }

    },
    getters: {
        getUserData(state) {
            return state.userData
        },
        getSavedList(state) {
            return state.userData.saved.videoIds
        },
        getPlaylistNames(state) {
            if (!state.userData.playlists) return null
            return state.userData.playlists

        },
        getFavList(state) {
            if (!state.userData.playlists) return null
            return state.userData.playlists.reduce((acc, playlist) => {
                return acc.concat(playlist.videoIds)
            }, [])
        },
        getFavoriteArtist(state) {
            if (!state.userData.userArtists) return null
            return state.userData.userArtists.map(artist => {
                return artist.youTubeId
            })
        }

    }
};
