<template>
  <section>
    <h2>{{title}}</h2>
    <div class="btn-grp" :style="{width:ww+'px'}">
      <button class="left" @click="leftSwipe">&#x0003C;</button>
      <button class="right" @click="rightSwipe">&#x0003E;</button>
    </div>
    <div class="container flex">
      <video-card v-for="(videoId, index) in videoIds" :key="index" :id="videoId">
        <div slot="btn" data-name="add" class="add" @click="onAddVideo(videoId)" 
        >Add to favorites</div>
        <div
          v-if="!savedList.includes(videoId)"
          slot="btn"
          data-name="save"
          class="save"
          @click="onSaveVideo(videoId)"
        >Save for later</div>
      </video-card>
    </div>
  </section>
</template>

<script>
import videoCard from "@/components/videoCard.vue";
export default {
  props: {
    title: String,
    videoIds: Array,
    index: Number
  },
  name: "video-row",
  components: {
    videoCard
  },
  data() {
    return {
      trans: 0,
      clickNum: 0,
      clicksAllowed: 0,
      size: 0,
      rowLength: 0,
    }
  },
  created() {
    this.rowLength = this.videoIds.length;
    if (window.innerWidth >= 600) {
      this.size = 374;
    } else this.size = 274;

    let distance = this.rowLength * this.size - window.innerWidth;
    this.clicksAllowed = Math.floor(distance / this.size);
  },
  methods: {
    leftSwipe() {
      const container = document.querySelectorAll(`.container`)[this.index];
      if (this.clickNum === 0) return;
      else if (this.clickNum > this.clicksAllowed) {
        this.trans +=
          (this.rowLength * this.size - window.innerWidth) % this.size;
        this.clickNum--;
      } else {
        this.trans += this.size;
        this.clickNum--;
      }
      container.style.transform = "translateX(" + this.trans + "px" + ")";
    },
    rightSwipe() {
      const container = document.querySelectorAll(`.container`)[this.index];

      if (this.clickNum === this.clicksAllowed) {
        this.trans -=
          (this.rowLength * this.size - window.innerWidth) % this.size;
        this.clickNum++;
      } else if (this.clickNum > this.clicksAllowed) {
        return;
      } else {
        this.trans -= this.size;
        this.clickNum++;
      }
      container.style.transform = "translateX(" + this.trans + "px" + ")";
    },
    onAddVideo(videoId) {
      this.$emit('onAddClicked', videoId)
    },

    onSaveVideo(videoId) {
      this.$store.dispatch({ type: "saveVideoForLater", videoId });
    }
  },
  computed: {
    ww() {
      if (window.innerWidth >= 600) return window.innerWidth - 15;
      else return window.innerWidth;
    },
    savedList() {
      return this.$store.getters.getSavedList
    },
    // favoriteList(){
    //   return this.$store.getters.getFavList
    // }
  }
};
</script>

<style lang="scss" scoped>
section {
  overflow-x: hidden;
  width: fit-content;
  margin-bottom: 30px;
  .container {
    transition: transform 0.3s ease-in;
    background-color: $rowBC;
    padding-right: 300px;
  }

  .btn-grp {
    display: flex;
    justify-content: space-between;
    button {
      background-color: $cardBC;
      color: $navC;
      padding: 5px 13px;
      border-radius: 5px;
      font-weight: 800;
      cursor: pointer;
    }
  }
}
.add-box {
  background-color: white;
  width: 250px;
  height: 200px;
  color: black;
  z-index: 1;
  position: absolute;
}
</style>

