<template>
  <nav>
    <ul class="flex space-between">
      <li data-name="myArtists" @click="setTab($event)">My Artists
         <transition name="fade">
        <ul key="1" v-if="activeTab==='myArtists'">
          <div v-for="(artist, index) in userData.userArtists" :key="index">
            <i class="fas fa-trash-alt" @click.stop="onRemoveList(artist, 'artist')"></i>
            <li @click.stop="onListChosen(artist, 'userArtists')">{{artist.name | capitalize}}</li>
          </div>
        </ul>
         </transition>
      </li>
      <li data-name="myPlaylists" @click="setTab($event)">My Playlists
        <transition name="fade">
        <ul key="2" v-if="activeTab==='myPlaylists'">
          <div v-for="(playlist, index) in userData.playlists" :key="index">
            <i class="fas fa-trash-alt" @click.stop="onRemoveList(playlist,'playlist')"></i>
            <li @click.stop="onListChosen(playlist, 'playlists')">{{playlist.name | capitalize}}</li>
          </div>
        </ul>
        </transition>
      </li>
      <li data-name="mySaved" @click="setTab($event)">Saved For Later</li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "music-nav",
  props: {
    userData: Object
  },
  data() {
    return {
      activeTab: ""
    };
  },
  methods: {
    setTab(ev) {
      if (this.activeTab !== "" && this.activeTab === ev.target.dataset.name) {
        this.activeTab = "";
      } else this.activeTab = ev.target.dataset.name;
      if (this.activeTab === "mySaved")
        this.onListChosen(this.userData.saved, "saved");
    },
    onListChosen(list, catagory) {
      this.$emit("listChosen", list, catagory);
    },
    onRemoveList(itemToRemove, type) {
      var textToDisplay = "";
      if (type === "artist"){
        textToDisplay = `The artist ${itemToRemove.name}`
      }
      else if (type === "playlist")
        textToDisplay = `The playlist ${itemToRemove.name}`
      this.$swal({
        title: "Are you sure?",
        text: "You will not be able to recover this file!",
        type: "warning",
        showCancelButton: true,
        confirmButtonText: "Yes, delete it!",
        cancelButtonText: "No, keep it"
      }).then(result => {
        if (result.value) {
          if (type === "artist") {
            this.$store
              .dispatch({ type: "removeArtist", artistToRemove:itemToRemove })
              .then(
                this.$swal(
                  "Deleted!",
                 textToDisplay+' was removed',
                  "success"
                )
              )
          }
          else if(type === "playlist") {
            this.$store.dispatch({type:"removePlaylist", playlistToRemove:itemToRemove})
            .then(
                this.$swal(
                  "Deleted!",
                 textToDisplay+' was removed',
                  "success"
                )
              )
          }
        } else if (result.dismiss === this.$swal.DismissReason.cancel) {
          this.$swal("Cancelled", "No changes were made.", "error");
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
nav {
  height: auto;
  background-color: $musicNavBC;
  ul {
    margin: 5px;
    li {
      padding: 5px;
      color: $navC;
      cursor: pointer;
      ul {
        div {
          margin: 10px 0;
          li {
            display: inline;
          }
          

        }
      }
    }
  }
  @media (min-width: 600px) {
    min-width: 280px;
    ul {
      margin: 10px 20px;
      flex-direction: column;
      font-size: 1.2rem;
      li {
        ul {
          margin-top: 20px;
          li {
            font-size: 0.9em;
          }
        }
      }
    }
  }
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
</style>

