<template>
  <nav :class="{ 'nav-active': isActive }">
    <div class="navbar-toggle" @click="toggleMenu">
      <i class="fas fa-bars"></i>
    </div>
    <div class="logo">MusicK</div>
    <ul class="col" :class="{ 'menu-active': isActive }">
      <li>
        <router-link to="/">{{'Home' |uppercase}}</router-link>
      </li>
      <li>
        <router-link to="/MyMusic">{{'My Music' |uppercase}}</router-link>
      </li>
      <li>
        <router-link to="/about">{{'About' |uppercase}}</router-link>
      </li>
      <li>
        <i class="fas fa-search" @click="onSearch"></i>
        <input type="text" placeholder="Search..." v-model="searchStr" @keyup.13="onSearch">
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "nav-bar",
  data() {
    return {
      isActive: false,
      searchStr:''
    };
  },
  methods: {
    toggleMenu() {
      this.isActive = !this.isActive;
    },
    onSearch(){
      this.$store.dispatch({type:'search', searchStr:this.searchStr})
      .then(()=>{
        if(this.$route.name!=='search'){
         this.$router.push('/Search')
        }
         this.searchStr=''
        
      })
    }
  }
};
</script>

<style lang="scss" scoped>
nav {
  background-color: $navBC;
  color: $navC;
  font-size: 1.4rem;
  position: relative;
  padding-bottom: 10px;
  ul {
    font-size: 0.9em;
    display: none;
    text-align: center;
    margin-top: 10px;
    li {
      border-top: 1px solid $border;
      padding-top: 10px;
      padding-bottom: 10px;
      transition: 0.3s;
     .fa-search{
       font-size: 1.5rem;
     }
      input {
        padding: 6px 8px;
        margin-left: 10px;
        border-radius: 6px;
        border: none;
        vertical-align: bottom;
      }
      &:hover {
        background-color: $menuHover;
        // color: #35fb00;
        font-size: 1.1em;
      }
      a {
        display: block;
      }
    }
  }
  ul:last-child {
    border-bottom: 1px solid $border;
  }
  .navbar-toggle {
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 20px;
  }
  .logo {
    font-family: "longhaul";
    margin-top: 10px;
    margin-left: 20px;
    display: inline-block;
    font-size: 1.25em;
  }
  .menu-active {
    display: block;
  }

  @media (min-width: 600px) {
    ul {
      
      position: absolute;
      top: 10px;
      right: 20px;
      display: flex;
      flex-direction: row;
      margin-top: 1px;
      li,
      a {
        border-top: 0;
        padding-top: 2px;
        padding-bottom: 0;
        margin: 0 15px;
        &:hover {
          font-size: 1em;
          background-color: $navBC;
        }
      }
    }
    ul:last-child {
      border-bottom: 0;
    }
    .navbar-toggle {
      display: none;
    }
  }
}
.nav-active {
  padding-bottom: 0;
}
</style>
