<template>
  <div class="home">
    <p>Create playlists with your favorite artists, and discover more</p>
    <add-modal
      v-if="isModalVisible"
      @close="closeModal"
      :playlists="playlistsNames"
      :videoId="videoIdToAdd"
    ></add-modal>
    <div class="loading" v-if="isLoading">Loading...</div>
    <div class="content" v-if="rowsData">
      <video-row
        v-for="(row, index) in rowsData"
        :key="index"
        :title="rowTitle + row.musicType"
        :videoIds="row.videoIds"
        :index="index"
        @onAddClicked="onAddClicked"
      ></video-row>
    </div>
  </div>
</template>

<script>
import videoRow from "@/components/videoRow.vue";
import addModal from "@/components/addModal.vue";
export default {
  name: "home",
  components: {
    videoRow,
    addModal
  },
  data() {
    return {
      searchStr: "",
      isLoading: false,
      rowsData: [],
      rowTitle: "Top five ",
      isModalVisible: false,
      videoIdToAdd: ""
    };
  },
  created() {
    this.isLoading = true;
    this.$store.dispatch({ type: "getTopFives" }).then(topFives => {
      this.rowsData = topFives;
      this.isLoading = false;
    });
  },
  methods: {
    sendRequest() {
      this.$store.dispatch({ type: "sendR", searchStr: this.searchStr });
    },
    onAddClicked(videoId) {
      this.videoIdToAdd = videoId
      this.showModal()
    },
    showModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    }
  },
  computed: {
    playlistsNames() {
      return this.$store.getters.getPlaylistNames;
    }
  }
};
</script>
<style lang="scss" scoped>
.home {
  overflow: hidden;
  p {
    text-align: center;
    font-size: 1.3em;
  }
  @media (min-width: 600px) {
    p{
      font-size: 1.5em;
    }
  }
}
</style>
