<template>
  <section>
    <add-modal
      v-if="isModalVisible"
      @close="closeModal"
      :playlists="playlistsNames"
      :videoId="videoIdToAdd"
    ></add-modal>
    <h1>{{searchResults.str!==''? `Results for: ` + capitalize():'Search for your favorit music'}}</h1>
    <div class="video-container flex wrap justify-center align-start">
      <video-card v-for="(video, index) in searchResults.results" :key="index" :id="video.videoId">
        <div slot="header" class="header">
          <h2
            :class="{ showMore: isShowMore===video.videoId }"
            @click="toggleShowMore(video.videoId)"
          >{{video.videoTitle}}</h2>
          <p>{{video.artistName | capitalize}}</p>
        </div>
        <div
          slot="btn"
          data-name="add"
          class="add"
          @click="onAddVideo(video.videoId)"
          v-if="!favoriteList.includes(video.videoId)"
        >Add to favorites</div>
        <div
          slot="btn"
          @click="onAddartist(video.artistId, video.artistName)"
          v-if="!artistsList.includes(video.artistId)"
        >Add artist</div>
        <div
          v-if="!savedList.includes(video.videoId)"
          slot="btn"
          data-name="save"
          class="save"
          @click="onSaveVideo(video.videoId)"
        >Save for later</div>
      </video-card>
    </div>
  </section>
</template>

<script>
import videoCard from "@/components/videoCard.vue";
import addModal from "@/components/addModal.vue";
export default {
  name: "search",
  components: {
    videoCard,
    addModal
  },
  data() {
    return {
      isModalVisible: false,
      videoIdToAdd: "",
      isShowMore: ""
    };
  },
  methods: {
    onSaveVideo(videoId) {
      this.$store.dispatch({ type: "saveVideoForLater", videoId });
    },
    onAddVideo(videoId) {
      this.videoIdToAdd = videoId;
      this.showModal();
    },
    onAddartist(artistId, artistName){
      this.$store.dispatch({type:'addArtist', artistToAdd:{id:artistId, name:artistName}})
      
    },
    showModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    },
    toggleShowMore(videoId) {
      if (this.isShowMore === videoId) this.isShowMore = "";
      else this.isShowMore = videoId;
    },
    capitalize(){
      let value=this.searchResults.str
      if (!value) return ''
    value = value.toString()
    return value.charAt(0).toUpperCase() + value.slice(1)
    }
  },
  computed: {
    searchResults() {
      return this.$store.getters.getSearchResults;
    },
    savedList() {
      return this.$store.getters.getSavedList;
    },
    favoriteList() {
      return this.$store.getters.getFavList;
    },
    playlistsNames() {
      return this.$store.getters.getPlaylistNames;
    },
    artistsList() {
      return this.$store.getters.getFavoriteArtist;
    }
  }
};
</script>

<style lang="scss" scoped>
section {
  // color: white;
  h1 {
    text-align: center;
  }
}
</style>

