<template>
  <section class="my-music-container flex col">
    <add-modal
      v-if="isModalVisible"
      @close="closeModal"
      :playlists="playlistsNames"
      :videoId="videoIdToAdd"
    ></add-modal>
    <music-nav :userData="userData" @listChosen="onListChosen"></music-nav>
    <section class="content">
      <h2 v-if="!chosenList.name">Choose an artist or a playlist to watch</h2>
      <div v-else>
        <h2>{{chosenList.name | capitalize}}</h2>
        <div class="video-container">
          <div v-if="chosenList.name==='saved'" class="flex wrap justify-center">
            <video-card v-for="(videoId, index) in chosenList.videoIds" :key="index" :id="videoId">
              <div
                slot="btn"
                data-name="add"
                class="add"
                @click="onAddVideo(videoId)"
              >Add to favorites</div>
              <div slot="btn" class="remove" @click="onRemoveVideo(videoId)">Remove</div>
            </video-card>
          </div>
          <div v-else class="flex wrap justify-center">
            <video-card v-for="(videoId, index) in chosenList.videoIds" :key="index" :id="videoId">
              <div slot="removBtn" class="remove" @click="onRemoveVideo(videoId)">Remove</div>
            </video-card>
          </div>
        </div>
      </div>
    </section>
  </section>
</template>

<script>
import MusicNav from "@/components/musicNavBar";
import VideoCard from "@/components/videoCard";
import addModal from "@/components/addModal.vue";
export default {
  components: {
    MusicNav,
    VideoCard,
    addModal
  },
  data() {
    return {
      chosenList: {
        name: ""
      },
      videoIdToAdd: "",
      isModalVisible: false
    };
  },

  methods: {
    onListChosen(playlist, catagory) {
      this.chosenList = playlist;
      this.chosenList.catagory = catagory;
    },
    showModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    },
    onRemoveVideo(videoId) {
      let videoToRemove = {
        videoId,
        catagory: this.chosenList.catagory,
        playlistId: this.chosenList.id
      };
      this.$store
        .dispatch({ type: "removeVideo", videoToRemove })
        .then(updatedData => (this.userData = updatedData));
    },
    onAddVideo(videoId) {
      this.videoIdToAdd = videoId;
      this.showModal();
    }
  },
  computed: {
    userData() {
      return this.$store.getters.getUserData;
    },
    playlistsNames() {
      return this.$store.getters.getPlaylistNames;
    },
    styles() {
      if (window.innerWidth < 600)
        return {
          width: "250px",
          height: "150px",
          display: "block"
        };
      return {
        width: "350px",
        height: "250px",
        display: "block"
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.my-music-container {
  min-height: 100vh;
  .content {
    text-align: center;
    .video-container {
      div {
        margin-bottom: 20px;
      }
    }
    h2{
      padding: 0 8px;
    }
  }
  @media (min-width: 600px) {
    flex-direction: row;
    music-nav {
      min-height: 100vh;
    }
    .content {
      margin: 0 auto;
      .video-container {
        div {
          margin: 10px;
        }
      }
    }
  }
}
</style>

