<template>
  <transition name="modal-fade">
    <section>
      <transition name="modal-fade">
        <div
          class="modal-backdrop flex justify-center align-center"
          @click.stop="closeOutside($event)"
        >
          <div class="modal flex col space-between">
            <!-- <div class="modal"> -->
            <section class="modal-body">
              <h2>Where to add it?</h2>
              <ul>
                <li v-for="(playlist, index) in playlists" :key="index">
                  <input type="checkbox" :id="playlist.id" v-model="checked" :value="playlist.id">
                  <label :for="playlist.id">{{playlist.name}}</label>
                </li>
                <li @click="showInput=!showInput">
                  <i class="fas fa-plus"></i>
                  Create new playlist
                </li>
                 <transition name="fade">
                <div v-if="showInput" class="flex col align-center">
                  <p>Playlist name:</p>
                    <input type="text" placeholder="Name" v-model="newPlaylistName" @keyup.13="addNewPlaylist">
                  <div @click="addNewPlaylist" >Add</div>
                </div>
                </transition>
              </ul>
            </section>
            <footer name="footer">
              <div @click="save">Save</div>
            </footer>
          </div>
        </div>
      </transition>
    </section>
  </transition>
</template>

<script>
export default {
  name: "add-modal",
  props: {
    playlists: Array,
    videoId: String
  },
  data() {
    return {
      checked: [],
      showInput: false,
      newPlaylistName:''
    };
  },
  created() {
    this.playlists.forEach(playlist => {
      if (playlist.videoIds.includes(this.videoId))
        this.checked.push(playlist.id);
    });
    this.checkedAtStart = this.checked;
  },
  methods: {
    save() {
      let changesToMake = {
        listsToAddTo: [],
        listsToRemoveFrom: [],
        videoId: this.videoId
      };
      this.playlists.forEach(currPlaylist => {
        if (
          this.checked.includes(currPlaylist.id) &&
          !this.checkedAtStart.includes(currPlaylist.id)
        )
          changesToMake.listsToAddTo.push(currPlaylist.id);
        else if (
          !this.checked.includes(currPlaylist.id) &&
          this.checkedAtStart.includes(currPlaylist.id)
        )
          changesToMake.listsToRemoveFrom.push(currPlaylist.id);
      });
      this.$store
        .dispatch({ type: "handleListsFromModal", changesToMake })
        .then(() => this.close());
    },
    close() {
      this.$emit("close");
    },
    closeOutside(ev) {
      if (ev.target.classList.contains("modal-backdrop")) this.close();
    },
    addNewPlaylist() {
      if(!this.newPlaylistName) return
      this.$store.dispatch({ type: "createNewPlaylist", playlistName:this.newPlaylistName});
    }
  }
};
</script>

<style lang="scss" scoped>
.modal-backdrop {
  position: fixed;

  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);

  background-color: rgba(0, 0, 0, 0.3);
  .modal {
    background: $modalBC;
    min-width: 300px;
    min-height: 350px;
    border-radius: 6px;
    color: $mainBC;
    box-shadow: 0 0 1pt 2pt $modalBorder;
    font-size: 1.15rem;
    left: auto;
    right: auto;
    .modal-body {
      position: relative;
      padding: 20px;
      h2 {
        text-align: center;
      }
      ul {
        li {
          margin: 3px 0;
          .fa-plus {
            margin: 10px 3px 0;
          }
        }
         div>*{
          margin: 5px 0
        }
        div {
          margin-top: 5px;
          input {
            border: 1px solid $border;
            padding: 3px 5px;
            border-radius: 3px;
            width: 11em;
          }
          div{
            border: 1px solid $actionBorder;
            padding: 5px 20px;
            border-radius: 10px;
            cursor: pointer;
          }
        }
       
      }
      ul:last-child {
        cursor: pointer;
      }
    }
    footer {
      border-top: 1px solid $navBC;
      div {
        padding: 5px 20px;
        border: 1px solid $actionBorder;
        border-radius: 10px;
        cursor: pointer;
        width: fit-content;
        text-align: center;
        margin: 20px auto;
      }
    }
  }
}
.modal-fade-enter,
.modal-fade-leave-active {
  opacity: 0;
}

.modal-fade-enter-active,
.modal-fade-leave-active {
  transition: opacity 0.1s ease;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>

